@use 'base/mixins' as m;
@use 'base/variables' as v;

.principal {
    &__tareas {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__vacio {
        text-align: center;
        font-family: v.$fprincipal;
        color: v.$naranja;
        font-size: 2.6rem;
        line-height: 1.1;
        font-weight: 900;
        @include m.tablet { 
            font-size: 3.5rem;
        }   
    }
    &__boton {
        @include m.boton(v.$rosa, v.$blanco, .8rem);
        border-radius: .5rem;
        margin: 0 auto;
        margin-top: 3rem;
        @include m.tablet { 
            max-width: 17rem;
        }
    }
    &__lista {
        margin: 2rem 0;
        display: grid;
        gap: 1rem;
        grid-template-rows: auto;
        padding-bottom: 3rem;
        @include m.tablet { 
            grid-template-columns: repeat(2, 1fr);
            margin: 2rem 0 0 0;
        }   
        @include m.desktop { 
            grid-template-columns: repeat(3, 1fr);
        }   
        a {
            color: v.$blanco;
            font-family: v.$fsecundaria;
            font-weight: 900;
            font-size: 1.8rem;
            text-align: center;
            @include m.tablet { 
                font-size: 2rem;
            }   
        }
    }
    &__proyecto {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 10rem;
        background-color: v.$azulf;
        border-radius: .3rem;
        padding: 1rem;
        transition: all ease-out 900ms;
        border: 1px solid v.$negro;
        @include m.tablet { 
            min-height: 14rem;
        }   
        &:hover {
            cursor: pointer;
            transform: scale(0.9) rotate(1deg);
            background-color: darken(v.$azulf, 100);
        }
    }
}