@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard {
    .formulario {
        padding: 3rem 0;
        &__campo {
            label {
                @include m.tablet { 
                    flex: 0 0 2rem;
                }   
                @include m.desktop { 
                    flex: 0 0 10rem;    
                }
            }
        }
    }
}