@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard {
    @include m.tablet { 
        height: 100vh;
        @include m.grid1-5(0);
    }   
    .principal1 {
        overflow: scroll;
        .contenido {
            height: 88%;
            @include m.tablet { 
                display: flex;
                flex-direction: column;
            }
            &__nombre {
                @include m.tablet { 
                    text-transform: uppercase;
                    padding: 3rem 0 1rem 0;
                    font-size: 4rem;
                    font-weight: 900;
                    font-family: v.$fsecundaria;
                }   
            }
        }
    }
}
