@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario {
    width: 100%;
    &__campo {
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;
        margin-top: 1rem;
        label {
            margin-bottom: .7rem;
            font-weight: 900;
        }
        input {
            padding: 1rem;
            background-color: lighten(v.$gris-oscuro, 50%);
            color: v.$negro;
            border: none;
            border-left: 5px solid v.$negro;
            margin-bottom: 1rem;
        }
        &.perfil {
            @include m.tablet { 
                label {
                    min-width: 14rem;
                }
            }
        }
    }
    @include m.desktop { 
        &__campo {
            flex-direction: row;
            align-items: center;
            label {
                flex: 0 0 10rem;
                margin-bottom: 0;
            }
            input {
                flex: 1;
            }
        }
    }
    &__boton {
        @include m.formbot(v.$rosa);
        border-radius: .5rem;
        min-width: 100%;
        @include m.tablet { 
            min-width: 18rem;
        }
    }
}

.alerta {
    margin-bottom: .5rem;
    text-align: center;
    color: v.$blanco;
    padding: 1rem;
    font-weight: 900;
    &.error {
        border-left: 6px solid v.$rojo;
        background-color: lighten(v.$rojo, 35%);
        &:last-of-type {
            margin-bottom: 0;
        }
    }
    &.exito {
        border-left: 6px solid v.$verde;
        background-color: lighten(v.$verde, 35%);
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}
.logo {
    width: 10rem;
    margin: 0 auto;
}
.pagina {
    @include m.tablet { 
        display: flex;
        flex-direction: column;
        height: 100vh;
    }
    .footer {
        margin-top: auto;
    }
}