@use 'base/mixins' as m;
@use 'base/variables' as v;

.confirma {
    .titulo {
        @include m.header(v.$marron);
    }
    .instrucciones__descripcion {
        color: v.$marron;
    }
    .formulario__boton {
        @include m.formbot(v.$marron);
        border-radius: .5rem;
    }
}