@use 'variables' as v; 
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fprincipal;
    font-size: 1.6rem;
    font-weight: 300;
}
[class$="__contenedor"] {
    max-width: 120rem;
    margin: 0 auto;
    width: 90%;
}
.contenedor2 {
    @include m.tablet { 
        width: 70%;
        margin: 0 auto;
    }
}
img {
    max-width: 100%;
    display: block;
}
ul {
    padding:0;
    list-style: none;
    margin: 0;
}
h1, h2, h3 {
    margin: 0;
    line-height: 1.2;
    text-align: center;
}
h1, h2 {
    font-weight: 900;
}
h1 {
    font-size: 4.4rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 2.4rem;
    font-weight: 300;
}
a {
    text-decoration: none;
    color: v.$negro;
}
p {
    font-size: 1.6rem;
    line-height: 1.8;
}