@use 'variables' as v; 

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin mqmodified {
    @media (min-width: 1800px) {
        @content;
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
@mixin grid1-2($espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: 1fr 2fr;
}
@mixin grid1-5($espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: 1fr 5fr;
}
@mixin grid2-1($espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: 2fr 1fr;
}


@mixin bgOpaco($url, $degradado) {
    background-image: linear-gradient($degradado 0%, $degradado 100%), url($url);
    background-repeat: no-repeat;
    background-size: cover;
}

@mixin scroll {
    overflow-y: hidden;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;

    > * {
        scroll-snap-align: center;
    }
}

@mixin boton($color, $texto, $medida) {
    background-color: $color;
    font-size: 2rem;
    padding: $medida;
    border-radius: 1rem;
    font-weight: 700;
    display: block;
    width: 100%;
    text-align: center;
    color: $texto;
    text-decoration: none;
    transition: all .2s ease;
    border: none;
    &:hover {
        cursor: pointer;
        background-color: lighten($color, 5);
    }
    @include tablet {
        width: auto;
    }
}

@mixin headLineaAtras($color) {
    display: inline-block;
    &::after {
        content: '';
        background-color: $color;
        height: 1rem;
        width: calc(100% + 1rem);
        display: block;
        margin: -1.4rem 0 0 0;
    } 
}

@mixin headLineaAbajo {
    &::after {
        content: '';
        display: block;
        height: 1rem;
        width: 15rem;
        margin-top: 1rem;
        background-image: linear-gradient(to right, v.$azulf 0%, v.$azulf 50%,
        lighten(v.$azulf, 20%) 50%, lighten(v.$azulf, 20%) 100%);
    }
}

@mixin headCursiva($color) {
    font-family: v.$fcursiva;
    font-weight: 900;
    color: v.$negro;
    font-size: 2rem;
    line-height: 1;
    span {
        display: block;
        font-family: v.$fsecundaria;
        color: $color;
    }
}

@mixin header ($color) {
    padding: 3rem 0;
    font-size: 5.5rem;
    font-family: v.$fsecundaria;
    font-weight: 900;
    background: linear-gradient(to right, $color 0%, lighten($color, 30%) 100%);
    color: transparent;
    background-clip: text;
        @include tablet { 
            font-size: 8rem;   
            padding-top: 7rem;
        }
}

@mixin acciones ($color) {
    @include headCursiva($color);
    display: flex;
    flex-direction: column;
    text-align: center;
    @include tablet { 
        flex-direction: row;
        justify-content: space-between;   
    }
    a {
        margin-bottom: 2.8rem;
        &:last-of-type {
            margin-bottom: 3.5rem;
        }
        @include tablet { 
            margin-bottom: 0;   
        }
    }
}

@mixin formbot ($color) {
    @include boton($color, v.$blanco, 1rem);
    margin: 0 auto;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    border-radius: 0;
    max-width: 25rem;
    @include tablet { 
        max-width: 25rem;   
    }
}