@use 'base/mixins' as m;
@use 'base/variables' as v;

.recuperar {
    .titulo {
        @include m.header(v.$azul);
    }
    .formulario__boton {
        @include m.formbot(v.$azul);
        border-radius: .5rem;
        min-width: 100%;
        margin: 0;
        @include m.tablet { 
            min-width: 18rem;
        }
    }
    .opciones-recupera {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        @include m.tablet { 
            margin-top: 2rem;
            flex-direction: row;
            justify-content: space-evenly;
        }
    }
}
.descripcion {
    font-family: v.$fsecundaria;
    font-weight: 900;
    font-size: 2.5rem;
    text-align: center;
    line-height: 1;
    margin-bottom: 5rem;
    @include m.tablet { 
        font-size: 2.5rem;   
    }
}