@use 'base/mixins' as m;
@use 'base/variables' as v;

.pagina {
    .titulo {
        @include m.header(v.$rosa);
    }
    .descripcion {
        font-family: v.$fsecundaria;
        font-weight: 900;
        font-size: 2.5rem;
        text-align: center;
        line-height: 1;
        margin-bottom: 5rem;
        @include m.tablet { 
            font-size: 2.5rem;   
        }
    }
}
.instrucciones {
    &__descripcion {
        text-align: center;
        font-family: v.$fsecundaria;
        font-weight: 900;
        text-transform: uppercase;
        font-size: 2rem;
        @include m.tablet { 
            margin-bottom: 5rem;   
        }
    }
    .acciones {
        @include m.acciones(v.$rosa)
    }
}