@use 'base/mixins' as m;
@use 'base/variables' as v;

.filtros {
    padding: 1.2rem; 
    background-color: rgba(57, 70, 89, 0.582);
    border-radius: .5rem;
    @include m.tablet { 
        display: flex;
        justify-content: space-between;
        gap: 1.5rem;
    }
    &__inputs {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        flex: 1;
    }
    &__titulo {
        font-size: 2rem;
        font-weight: 900;
        text-align: center;
        margin-bottom: 1.5rem;
        font-family: v.$fsecundaria;
        color: v.$blanco;
        @include m.tablet { 
            margin-bottom: 0;
        }
    }
    .campo {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: center;
        @include m.tablet { 
            flex-direction: row;
        }
        label {
            font-family: v.$fsecundaria;
            color: v.$blanco;
            font-weight: 600;
            cursor: pointer;
            @include m.tablet { 
                margin-right: .3rem;
            }
        }
    }
}