@use 'base/variables' as v;
@use 'base/mixins' as m;

.barrahead {
    padding: 2rem 0;
    margin: 0 0 2rem 0;
    @include m.tablet {
        padding: 1.3rem 0;
        height: 7.5rem;
        border-bottom: 1px solid v.$negro;
        -webkit-box-shadow: 0px 4px 26px 1px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 4px 26px 1px rgba(0,0,0,0.75);
        box-shadow: 0px 4px 26px 1px rgba(0,0,0,0.75);
    }
}
.barra {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 0 auto;
    width: 90%;
    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;   
        align-items: center;
        width: 97%;
    }
    &__nombre {
        font-weight: 900;
        color: v.$blanco;
        padding: .8rem;
        margin-bottom: 1.5rem;
        background-color: rgba(57, 70, 89, 0.582);
        @include m.tablet {
            margin-bottom: 0;
            border-bottom-left-radius: 2rem;
            border-top-right-radius: 2rem;
        }
        &:hover {
            cursor: no-drop;
        }
    }
    &__nombre p {
        font-size: 1.8rem;
        margin: 0;
    }
    &__boton {
        transition: 1s ease all;
        padding: 1rem;
        background-color: rgba(57, 70, 89, 0.582);
        @include m.tablet {
            border-top-left-radius: 2rem;
            border-bottom-right-radius: 2rem;
        }
        &:hover {
            cursor: pointer;
            @include m.tablet {
                transform: scale(1.1);
            }
        }
    }
    &__boton a {
        color: v.$blanco;
        font-size: 1.8rem;
        font-weight: 900;   
    }
}