@use 'base/mixins' as m;
@use 'base/variables' as v;

.principal {   
    &__tareas {
        margin: 1.5rem 0 2rem 0;
    }
    &__boton-tareas {
        @include m.boton(v.$rosa, v.$blanco, .8rem);
        border-radius: .5rem;
        @include m.tablet { 
            min-width: 18rem;
        }
    }
    &__boton-eliminar {
        @include m.boton(v.$rojo, v.$blanco, .8rem);
        border-radius: .5rem;
        @include m.tablet { 
            min-width: 18rem;
        }
    }
    .listado-tareas {
        list-style: none;
        margin-top: 2rem;
        .no-tareas {
            text-align: center;
            font-size: 2.6rem;
            padding: 4rem 0;
            font-family: v.$fprincipal;
            font-weight: 900;
            color: v.$naranja;
            @include m.desktop { 
                font-size: 3.5rem;
            }
        }
        .tarea {
            display: flex;
            flex-direction: column;
            align-items: center;
            border-bottom: 2px solid v.$negro;
            margin-bottom: 2rem;
            @include m.tablet { 
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
            }
            p {
                cursor: pointer;
                font-weight: 900;
                font-size: 2rem;
            }
        }
        .opciones {
            display: flex;
            margin-bottom: 1.5rem;
            gap: 1rem;
            button {
                border: none;
                background-color: unset;
                padding: .8rem;
                border-radius: 2rem;
                transition: all ease-in-out 300ms;
                &:hover {
                    cursor: pointer;
                }
            }
            .estado-tarea {
                color: v.$blanco;
                min-width: 10.5rem;  
                font-weight: 900;
            }
            .pendiente {
                background-color: v.$naranja;
                &:hover {
                    background-color: darken(#ff8c00, 8);
                }
            }
            .completado {
                background-color: v.$verde;
                &:hover {
                    background-color: darken(#069706, 8);
                }
            }
            .eliminar-tarea {
                background-color: v.$rojo;
                color: v.$blanco;
                font-weight: 900;
                min-width: 10.5rem;
                &:hover {
                    background-color: darken(#990000, 8);
                }
            }
        }
    }
}