@use 'base/mixins' as m;
@use 'base/variables' as v;

@media (prefers-color-scheme: dark) {
    body {
        background-color: v.$gris-oscuro;
    }
    .pagina {
        .descripcion {
            color: v.$gris;
        }
        .alerta.error {
            color: v.$gris;
            background-color: #640202;
        }
        .alerta.exito {
            color: v.$gris;
            background-color: #015001;
        }
        &.olvide {
            .formulario__boton {
                color: v.$gris;
                background-color: #830000;
            }
            .acciones {
                span {
                    color: #830000;
                }
            }
        }
        &.login {
            .formulario__boton {
                color: v.$gris;
                background-color: #85174e;
            }
            .acciones {
                span {
                    color: #85174e;
                }
            }
        }
        &.registrar {
            .formulario__boton {
                color: v.$gris;
                background-color: #450f61;
            }
            .acciones {
                span {
                    color: #450f61;
                }
            }
        }
        &.mensaje {
            .instrucciones {
                &__descripcion {
                    color: #045177;
                }
            }
            .formulario__boton {
                color: v.$gris;
                background-color: #212b3b;
            }
            
        }
        &.confirma {
            .formulario__boton {
                color: v.$gris;
                background-color: #85174e;
            }
        }
        &.recuperar {
            .formulario__boton {
                color: v.$gris;
                background-color: #044566;
            }
        }
    }
    .instrucciones {
        &__descripcion {
            color: v.$gris;
        }
        .acciones {
            a {
                color: v.$gris;
            }
        }
    }
    .footer {
        &__titulo {
            color: v.$gris;
        }
    }
    .formulario {
        &__campo {
            label {
                color: v.$gris;
            }
        }
    }
    .dashboard {
        .sidebar {
            &__nombre {
                color: v.$gris;
            }
            &__enlaces {
                color: v.$gris;
            }
        }
        .principal1 {
            .contenido {
                &__nombre {
                    color: v.$gris;
                }
            }
            .barra {
                &__nombre {
                    color: v.$gris;
                    background-color: #22314794;
                }
                &__boton {
                    background-color: #22314794;
                    a {
                        color: v.$gris;
                    }
                }
            }
            .boton-form {
                color: v.$gris;
                background-color: #85174e;
            }
        }
        .principal {
            &__proyecto {
                color: v.$gris;
            }
            &__boton-tareas {
                color: v.$gris;
                background-color: #85174e;
            }
            &__boton-eliminar {
                color: v.$gris;
                background-color: #640202;
            }      
            .listado-tareas {
                .tarea-nombre {
                    color: v.$gris;
                }
                .opciones {
                    .estado-tarea {
                        color: v.$gris;
                    }
                    .completado {
                        background-color: #013d01;
                    }
                    .pendiente {
                        background-color: #683a02;
                    }
                    .eliminar-tarea {
                        color: v.$gris;
                        background-color: #640202;
                    }
                }
            }
            .boton-pswrd {
                color: v.$gris;
                background-color: #640202;
            }
            .principal__boton {
                color: v.$gris;
                background-color: #85174e;
            }
            .alerta.error {
                color: v.$gris;
                background-color: #640202;
            }
            .alerta.exito {
                color: v.$gris;
                background-color: #015001;
            }
        }
    }
    .modal {
        .formulario {
            background-color: v.$gris-oscuro;
            legend {
                color: v.$gris;
            }
            .campo-mod {
                label {
                    color: v.$gris;
                }
            }
            .opciones {
                .submit-nueva-tarea {
                    color: v.$gris;
                    background-color: #013d01;
                }
                .cerrar-modal {
                    color: v.$gris;
                    background-color: #640202;
                }
            }
        }
    }
}