@use 'base/mixins' as m;
@use 'base/variables' as v;

.modal {
    padding: 0 1rem;
    position: absolute;
    background-color: rgb(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    @include m.tablet { 
        padding: 0;
    }
    .formulario {
        max-width: 80rem;
        background-color: v.$blanco;
        border-radius: 2px;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 1000ms;
        transition-timing-function: ease-in-out;
        opacity: 0;
        legend {
            text-align: center;
            font-family: v.$fsecundaria;
            font-size: 2rem;
            font-weight: 900;
            text-transform: uppercase;
            margin-bottom: 2rem;
            display: block;
        }
        .campo-mod {
            display: flex;
            flex-direction: column;
            width: 100%;
            label {
                font-weight: 900;
                font-family: v.$fsecundaria;
                text-transform: uppercase;
                font-size: 1.8rem;
                margin-bottom: .5rem;
            }
            input {
                font-weight: 900;
                text-align: center;
                padding: 1rem;
                border: none;
                margin-bottom: 2rem;
                background-color: rgb(225, 225, 225);
            }
        }
        .opciones {
            display: flex;
            justify-content: center;
            column-gap: 1rem;
        }
        .submit-nueva-tarea {
            @include m.boton(v.$verde, v.$blanco, 1rem);
            border-radius: 1px;
        }
        .cerrar-modal {
            @include m.boton(v.$rojo, v.$blanco, 1rem);
            border-radius: 1px;
        }
        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar {
            transform: translateY(-30rem);
            opacity: 0;
        }
        .alerta {
            max-width: 100%;
            margin: 1rem 0;
        }
    }
}