@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: v.$azulf;
    padding: 4rem 0 2rem 0;
    @include m.tablet { 
        height: 100vh; 
    }
    &__nombre {
        font-size: 6rem;
        color: v.$blanco;
        font-weight: 900;
        font-family: v.$fsecundaria;
        padding: 0 0 3.5rem 0;
        @include m.tablet { 
            padding: 0 1rem;
        }   
    }
    &__navegacion {
        margin-top: 0;
        display: flex;
        flex-direction: column;
        padding: 1rem 1rem;
        @include m.tablet { 
            padding: 1rem 1rem;
        }   
        @include m.mqmodified { 
            padding: 7rem 2rem 0 3rem;
        }
    }
    &__navegacion a {
        padding: 1rem;
        font-family: v.$fsecundaria;
        font-weight: 900;
        margin-bottom: 1rem;
        text-align: center;
        text-transform: uppercase;
        color: v.$blanco;
        font-size: 1.5rem;
        transition: all ease-in 400ms;
        border-radius: .5rem;
        &:hover, &.activo {
            background-color: rgb(0, 0, 0, 0.5);
        }
        @include m.tablet { 
            text-align: justify;
            padding: 1rem;
            margin-bottom: 2.2rem;
        }
    }
    .logo {
        padding: 1rem 0;
    }
}