@use 'base/mixins' as m;
@use 'base/variables' as v;

.mensaje {
    .titulo {
        @include m.header(v.$azulf);
    }
    .instrucciones__descripcion {
        color: v.$azul;
    }
    .formulario__boton {
        @include m.formbot(v.$azulf);
        border-radius: .5rem;
    }
}