@use 'base/mixins' as m;
@use 'base/variables' as v;

.boton-pswrd {
    @include m.boton(v.$rojo, v.$blanco, .8rem);
    border-radius: .5rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
    @include m.tablet { 
        max-width: 20rem;
        margin-left: auto;
    }
}
.boton-form {
    @include m.boton(v.$rosa, v.$blanco, .8rem);
    border-radius: .5rem;
    margin: 0 auto;
    margin-top: 2.5rem;
}