@use 'base/mixins' as m;
@use 'base/variables' as v;

.registrar {
    .titulo {
        @include m.header(v.$morado);
    }
    .acciones {
        @include m.acciones(v.$morado);
    }
    .formulario__boton {
        @include m.formbot(v.$morado);
        border-radius: .5rem;
    }
}
.descripcion {
    font-family: v.$fsecundaria;
    font-weight: 900;
    font-size: 2.5rem;
    text-align: center;
    line-height: 1;
    margin-bottom: 5rem;
    @include m.tablet { 
        font-size: 2.5rem;   
    }
}